import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { UserService } from '../../../services/user.service';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-user-membership',
  standalone: true,
  imports: [CommonModule,],
  templateUrl: './user-membership.component.html',
  styleUrl: './user-membership.component.scss'
})
export class UserMembershipComponent {
  user: any | null = null;
  hasSubscription: boolean | null = null;
  validTill: any;
  constructor(private http: HttpClient, private userService: UserService) { }
  ngOnInit(): void {
    this.userService.getUser().subscribe(user => {
      this.user = user;
      console.log("user in home", this.user);
      this.hasSubscription = user?.hasSubscription || false;
      if (this.user?.subscriptionType === 'annual' && this.user?.memberShipStartedAt) {
        const start = new Date(this.user.memberShipStartedAt);
        const expiry = new Date(start);
        expiry.setFullYear(start.getFullYear() + 1);
        this.validTill = expiry.toISOString(); // store as ISO string
      } else if (this.user?.subscriptionType === 'lifetime') {
        this.validTill = 'lifetime';
      } else {
        this.validTill = null; // for safety, if no subscription
      }
    });
  }
  paymentbtn(value: string) {
    console.log("Button clicked with value:", value);
    const url = `${window.location.origin}/user-home/payment-status`;
    const payload = {
      memberShip: value,
      clientUrl: url
    }
    this.http.post<any>(`${environment.apiUrl}/orders/initiate`, payload)
      .subscribe({
        next: (res) => {
          console.log(res);
          const paymentUrl = res.data.data;
          window.open(paymentUrl, '_self');
        },
        error: (err) => {
          console.error('Error fetching Events:', err);
        }
      });
  }
}
